* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  --hover-color: #00acdc;
}

.header {
  background-color: #fff;
  justify-content: space-between;
  width: 100%;
  padding: 0 50px;
  display: flex;
}

.logo_link {
  align-self: center;
  line-height: 0;
}

.nav__list {
  display: flex;
}

.nav__list__content {
  margin-left: 20px;
  font-family: Roboto, sans-serif;
  font-style: normal;
  font-weight: 500;
  list-style-type: none;
}

.nav__link {
  color: #000;
  text-transform: uppercase;
  font-size: 12px;
  line-height: 60px;
  text-decoration: none;
  display: inline-block;
}

.nav__list__content:first-child {
  margin-left: 0;
}

.nav__link:hover {
  color: var(--hover-color);
}

.is-active {
  color: var(--hover-color);
  position: relative;
}

.is-active:after {
  content: "";
  background-color: var(--hover-color);
  color: var(--hover-color);
  width: 100%;
  height: 4px;
  position: absolute;
  bottom: 0;
  right: 0;
}
